  color: #fff;
  padding: 0.3rem 0.8rem;
  font-size: 0.9rem;
  border-radius: 3px;
  outline: none!important;
  font-weight: 500;
}
.btn:hover,
.btn:focus {
  outline: none!important;
  box-shadow: none;
}
.btn-primary {
  background-color: #3a52ff;
  border-color: #3a52ff;
}
.btn-primary:hover {
  background-color: #6376ff;
  border-color: #6376ff;
}
.btn-primary.btn-shadow {
  box-shadow: 0 3px 4px rgba(58, 82, 255, 0.4);
}
.btn-primary.btn-shadow:hover {
  box-shadow: none;
}
.btn-primary.btn-styled {
  color: #fff;
  background-color: #3a52ff;
}
.btn-primary.btn-styled::after {
  background-color: #fff;
}
.btn-primary.btn-styled:hover {
  color: #3a52ff;
  border-color: #3a52ff;
}
.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle {
  border-color: rgba(58, 82, 255, 0.08);
  background-color: #2640ff;
}
.btn-primary:not(:disabled):not(.disabled):active:focus {
  box-shadow: 0 0 0 0.2rem rgba(58, 82, 255, 0.3);
}
