/***************** 2.15) SPINNERS ****************/
.loader-dot {
  margin: auto;
}
.loader-dot span {
  display: inline-block;
  background-color: #3a52ff;
  margin-right: 10px;
  border-radius: 100%;
  width: 14px;
  height: 14px;
}
.loader-dot span:nth-child(1) {
  animation: loader-dots 1s ease-in-out 0.2s infinite;
}
.loader-dot span:nth-child(2) {
  animation: loader-dots 1s ease-in-out 0.4s infinite;
}
.loader-dot span:nth-child(3) {
  animation: loader-dots 1s ease-in-out 0.6s infinite;
}
.loader-dot span:nth-child(4) {
  animation: loader-dots 1s ease-in-out 0.8s infinite;
}
@keyframes loader-dots {
  0%,
  100% {
    transform: scale(0.4);
  }
  50% {
    transform: scale(1);
  }
}
@-webkit-keyframes loader-dots {
  0%,
  100% {
    transform: scale(0.4);
  }
  50% {
    transform: scale(1);
  }
}
.loader-propeller {
  display: inline-block;
  position: relative;
  width: 50px;
  height: 50px;
  margin: auto;
}
.loader-propeller::after {
  content: '';
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  border: 20px solid #3a52ff;
  border-color: #3a52ff transparent #3a52ff transparent;
  animation: loader-propeller 1.5s infinite;
}
@keyframes loader-propeller {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.1, 0.055, 0.675, 0.2);
  }
  50% {
    transform: rotate(800deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1600deg);
  }
}
@-webkit-keyframes loader-propeller {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.1, 0.055, 0.675, 0.2);
  }
  50% {
    transform: rotate(800deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1600deg);
  }
}
.loader-ripple {
  display: inline-block;
  position: relative;
  width: 60px;
  height: 60px;
  margin: auto;
}
.loader-ripple div {
  position: absolute;
  border: 4px solid #3a52ff;
  opacity: 1;
  filter: alpha(opacity=100);
  border-radius: 50%;
  animation: loader-ripple 1.1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.loader-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes loader-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
    filter: alpha(opacity=100);
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
    filter: alpha(opacity=0);
  }
}
@-webkit-keyframes loader-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
    filter: alpha(opacity=100);
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
    filter: alpha(opacity=0);
  }
}
.loader-bar {
  margin: 100px auto;
  width: 45px;
  height: 45px;
  text-align: center;
}
.loader-bar > div {
  display: inline-block;
  background-color: #3a52ff;
  height: 100%;
  width: 5px;
  animation: loader-bar 1.2s infinite ease-in-out;
}
.loader-bar div:nth-child(2) {
  animation-delay: -1.1s;
}
.loader-bar div:nth-child(3) {
  animation-delay: -1s;
}
.loader-bar div:nth-child(4) {
  animation-delay: -0.9s;
}
.loader-bar div:nth-child(4) {
  animation-delay: -0.8s;
}
@keyframes loader-bar {
  0%,
  40%,
  100% {
    -webkit-transform: scaleY(0.4);
  }
  20% {
    -webkit-transform: scaleY(1);
  }
}
@-webkit-keyframes loader-bar {
  0%,
  40%,
  100% {
    -webkit-transform: scaleY(0.4);
  }
  20% {
    -webkit-transform: scaleY(1);
  }
}
.loader-ring {
  position: relative;
  display: inline-block;
  width: 45px;
  height: 45px;
  margin: auto;
}
.loader-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 40px;
  height: 40px;
  border: 3px solid #3a52ff;
  border-radius: 50%;
  animation: loader-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #3a52ff transparent transparent transparent;
}
.loader-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.loader-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.loader-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes loader-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
