.control {
  display: block;
  position: relative;
  padding-left: 25px;
  line-height: 24px;
  margin-bottom: 10px;
  cursor: pointer;
}
.control input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  filter: alpha(opacity=0);
}
.control span {
  font-size: 14px;
  color: #474648;
}
.control-icon {
  position: absolute;
  top: 5px;
  left: 0;
  height: 14px;
  width: 14px;
  background-color: #ebebeb;
  transition: all 600ms;
}
.control-icon::after {
  content: '';
  position: absolute;
  display: none;
}
.control-radio .control-icon {
  border-radius: 50%;
}
.control-radio .control-icon::after {
  left: 4px;
  top: 4px;
  height: 6px;
  width: 6px;
  background-color: #fff;
  border-radius: 50%;
}
.control-radio input:disabled ~ .control-icon::after {
  background-color: #7b7b7b;
}
.control-checkbox .control-icon {
  border-radius: 3px;
}
.control-checkbox .control-icon::after {
  content: "\f00c";
  font-family: "Font Awesome 5 Free";
  color: #fff;
  font-weight: 600;
  font-size: 10px;
  top: -4px;
  left: 3px;
}
.control:hover input ~ .control-icon,
.control input:focus ~ .control-icon {
  background-color: #ebebeb;
}
.control input:checked ~ .control-icon {
  background-color: #3a52ff;
}
.control:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #0725ff;
}
.control input:disabled ~ .control-icon {
  background-color: #e6e6e6;
  opacity: 0.6;
  pointer-events: none;
}
.control input:disabled ~ .control-icon::after {
  color: #a8a8a8;
}
.control input:checked ~ .control-icon::after {
  display: block;
}
.control-success input:checked ~ .control-icon {
  background-color: #48ba16;
}
.control-success:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #368c11;
}
.control-danger input:checked ~ .control-icon {
  background-color: #DB3847;
}
.control-danger:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #be2231;
}
.control-warning input:checked ~ .control-icon {
  background-color: #ffc107;
}
.control-warning:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #d39e00;
}
.control-info input:checked ~ .control-icon {
  background-color: #308af3;
}
.control-info:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #0d70e3;
}
.control-dark input:checked ~ .control-icon {
  background-color: #4d4d4d;
}
.control-dark:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #333333;
}
.control-secondary input:checked ~ .control-icon {
  background-color: #DF0241;
}
.control-secondary:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #ac0232;
}
.control-purple input:checked ~ .control-icon {
  background-color: #ad0af9;
}
.control-purple:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #8c05cb;
}
.control-cyan input:checked ~ .control-icon {
  background-color: #00FFFF;
}
.control-cyan:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #00cccc;
}
.control-pink input:checked ~ .control-icon {
  background-color: #f96197;
}
.control-pink:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #f73077;
}
.control-brown input:checked ~ .control-icon {
  background-color: #885644;
}
.control-brown:hover input:not([disabled]):checked ~ .control-icon {
  background-color: #664133;
}
.control-light input:checked ~ .control-icon {
  background-color: rgba(222, 222, 222, 0.8);
}
.control-light:hover input:not([disabled]):checked ~ .control-icon {
  background-color: rgba(196, 196, 196, 0.8);
}
.switch {
  position: relative;
  display: inline-block;
  width: 38px;
  height: 22px;
}
.switch input {
  position: absolute;
  left: -9999px;
  width: 0;
  height: 0;
}
.switch input:checked + .switch-toggle {
  background-color: #3a52ff;
}
.switch input:checked + .switch-toggle:before {
  transform: translateX(18px);
}
.switch .switch-toggle {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: -4px;
  right: 0;
  bottom: 0;
  background-color: #ebebeb;
  border-radius: 100px;
  transition: all 300ms;
}
.switch .switch-toggle::before {
  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  border-radius: 50%;
  transition: all 300ms;
}
.switch-success input:checked + .switch-toggle {
  background-color: #48ba16;
}
.switch-danger input:checked + .switch-toggle {
  background-color: #DB3847;
}
.switch-warning input:checked + .switch-toggle {
  background-color: #ffc107;
}
.switch-info input:checked + .switch-toggle {
  background-color: #308af3;
}
.switch-dark input:checked + .switch-toggle {
  background-color: #4d4d4d;
}
.switch-purple input:checked + .switch-toggle {
  background-color: #ad0af9;
}
.switch-cyan input:checked + .switch-toggle {
  background-color: #00FFFF;
}
.switch-pink input:checked + .switch-toggle {
  background-color: #f96197;
}
.switch-brown input:checked + .switch-toggle {
  background-color: #885644;
}
.switch-outline input:checked + .switch-toggle {
  background-color: transparent;
}
.switch-outline input:checked + .switch-toggle::before {
  background-color: #3a52ff;
  box-shadow: 0 5px 20px rgba(58, 82, 255, 0.2);
  transform: translateX(18px);
}
.switch-outline .switch-toggle {
  background-color: transparent;
  border: 1px solid #dbdbdb;
}
.switch-outline .switch-toggle::before {
  background-color: #ebebeb;
  bottom: 3px;
}
.switch-outline.switch-success input:checked + .switch-toggle {
  background-color: transparent;
}
.switch-outline.switch-success input:checked + .switch-toggle::before {
  background-color: #48ba16;
  box-shadow: 0 5px 20px rgba(72, 186, 22, 0.2);
}
.switch-outline.switch-danger input:checked + .switch-toggle {
  background-color: transparent;
}
.switch-outline.switch-danger input:checked + .switch-toggle::before {
  background-color: #DB3847;
  box-shadow: 0 5px 20px rgba(219, 56, 71, 0.2);
}
.switch-outline.switch-warning input:checked + .switch-toggle {
  background-color: transparent;
}
.switch-outline.switch-warning input:checked + .switch-toggle::before {
  background-color: #ffc107;
  box-shadow: 0 5px 20px rgba(255, 193, 7, 0.2);
}
.switch-outline.switch-info input:checked + .switch-toggle {
  background-color: transparent;
}
.switch-outline.switch-info input:checked + .switch-toggle::before {
  background-color: #308af3;
  box-shadow: 0 5px 20px rgba(48, 138, 243, 0.2);
}
.switch-outline.switch-dark input:checked + .switch-toggle {
  background-color: transparent;
}
.switch-outline.switch-dark input:checked + .switch-toggle::before {
  background-color: #4d4d4d;
  box-shadow: 0 5px 20px rgba(77, 77, 77, 0.2);
}
.switch-outline.switch-purple input:checked + .switch-toggle {
  background-color: transparent;
}
.switch-outline.switch-purple input:checked + .switch-toggle::before {
  background-color: #ad0af9;
  box-shadow: 0 5px 20px rgba(173, 10, 249, 0.2);
}
.switch-outline.switch-cyan input:checked + .switch-toggle {
  background-color: transparent;
}
.switch-outline.switch-cyan input:checked + .switch-toggle::before {
  background-color: #00FFFF;
  box-shadow: 0 5px 20px rgba(0, 255, 255, 0.2);
}
.switch-outline.switch-pink input:checked + .switch-toggle {
  background-color: transparent;
}
.switch-outline.switch-pink input:checked + .switch-toggle::before {
  background-color: #f96197;
  box-shadow: 0 5px 20px rgba(249, 97, 151, 0.2);
}
.switch-outline.switch-brown input:checked + .switch-toggle {
