/***************** 4.5) TIMELINE ****************/
.timeline {
  position: relative;
  width: 80%;
  margin: 0 auto;
  padding: 30px 0;
}
.timeline::after {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  width: 2px;
  height: 100%;
  background-color: #3a52ff;
}
.timeline-item {
  position: relative;
  left: 0;
  width: 50%;
  min-width: 50%;
  padding-right: 40px;
  margin-bottom: 80px;
  margin-top: 20px;
}
.timeline-item:last-child {
  margin-bottom: 0;
}
.timeline-item:before,
.timeline-item:after {
  content: "";
  display: table;
}
.timeline-item:after {
  clear: both;
}
.timeline-item-right {
  left: 50%;
  padding-left: 40px;
}
.timeline-item-right .timeline-detail {
  right: auto;
  left: 38px;
}
.timeline-item-right .timeline-point {
  position: absolute;
  left: -7px;
}
.timeline-item-right .timeline-content::after {
  left: -8px;
  border-left: 0!important;
  border-right: 8px solid #fff;
}
.timeline-item .timeline-content {
  position: relative;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.08);
  border-radius: 6px;
  transition: all 600ms;
}
.timeline-item .timeline-content::after {
  content: '';
  position: absolute;
  top: 15px;
  right: -8px;
  width: 0;
  height: 0;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-left: 8px solid #fff;
}
.timeline-item .timeline-content h5,
.timeline-item .timeline-content .timeline-title {
  color: #2e2d2f;
  font-weight: 600;
}
.timeline-item .timeline-content p,
.timeline-item .timeline-content .timeline-text {
  margin: 0;
  color: #888888;
}
.timeline-item .timeline-detail {
  position: absolute;
  right: 38px;
  margin-top: -40px;
}
.timeline-item .timeline-detail span {
  font-size: 12px;
  font-weight: 500;
  color: #b1b0b0;
}
.timeline-item .timeline-point {
  position: absolute;
  top: 10px;
  right: -9px;
}
.timeline-item .timeline-point .badge {
  border: 3px solid rgba(255, 255, 255, 0.5);
}
@keyframes timelinePoint {
  0% {
    border: 3px solid rgba(255, 255, 255, 0.2);
  }
  50% {
    border: 3px solid rgba(255, 255, 255, 0.5);
    transform: scale(1.3);
  }
  100% {
    border: 3px solid rgba(255, 255, 255, 0.8);
    transform: scale(1.6);
  }
}
@-webkit-keyframes timelinePoint {
  0% {
    border: 3px solid rgba(255, 255, 255, 0.2);
  }
  50% {
    border: 3px solid rgba(255, 255, 255, 0.5);
    transform: scale(1.3);
  }
  100% {
    border: 3px solid rgba(255, 255, 255, 0.8);
    transform: scale(1.6);
  }
}
.timeline-item:hover .timeline-content {
  background-color: #fff;
}
.timeline-item:hover .timeline-point .badge {
  -webkit-animation: timelinePoint 0.5s linear infinite alternate;
  animation: timelinePoint 0.5s linear infinite alternate;
}
.timeline .img-list {
  display: flex;
}
.timeline .img-list .avatar {
  margin-right: 5px;
}
@media (max-width:991px) {
  .timeline {
    width: 100%;
  }
  .timeline::after {
    left: 0;
  }
  .timeline .timeline-item {
    width: 100%;
    padding-left: 40px;
    padding-right: 0;
  }
  .timeline .timeline-item .timeline-point {
    right: auto;
    left: -7px;
  }
  .timeline .timeline-item .timeline-detail {
    right: auto;
    left: 40px;
  }
  .timeline .timeline-item .timeline-content::after {
    border-left: 0!important;
    border-right: 8px solid #fff;
    left: -8px;
  }
  .timeline .timeline-item-right {
    left: 0;
  }
}
